/* ==========================================================================
# Side area style - begin
========================================================================== */

.eltd-light-header .eltd-page-header > div:not(.eltd-sticky-header):not(.fixed) .eltd-side-menu-button-opener .eltd-side-menu-icon span,
.eltd-light-header .eltd-top-bar .eltd-side-menu-button-opener .eltd-side-menu-icon span {
	background-color: $header-light-color !important; //Color can be set inline from widget opener;
	
	&.opened,
	&:hover {
		background-color: $header-light-hover-color !important; //Color can be set inline from widget opener;
	}
}

.eltd-dark-header .eltd-page-header > div:not(.eltd-sticky-header):not(.fixed) .eltd-side-menu-button-opener .eltd-side-menu-icon span,
.eltd-dark-header .eltd-top-bar .eltd-side-menu-button-opener .eltd-side-menu-icon span {
    background-color: $header-dark-color !important; //Color can be set inline from widget opener;
	
	&.opened,
	&:hover {
        background-color: $header-dark-hover-color !important; //Color can be set inline from widget opener;
	}
}

.eltd-side-menu-button-opener {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	
	.eltd-side-menu-title {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		margin: 0 8px 0 0;
		color: currentColor;
	}
	
	.eltd-side-menu-icon {
		position: relative;
		display: block;
		height: 14px;
        width: 20px;
		
		span {
			display: block;
            position: absolute;
            left: 0;
            height: 1px;
            width: 100%;
            background-color: $default-heading-color;
            @include eltdTransition(background-color 0.3s ease-out);
			line-height: inherit;

            &:nth-child(1){
                top:0;
            }

            &:nth-child(2) {
                top:50%;
            }

            &:nth-child(3) {
                top:100%;
            }

		}
	}
	
	&.opened,
	&:hover {
        .eltd-side-menu-icon span {
            opacity:0.8;
        }
	}
}

.eltd-side-menu {
    position: fixed;
    top: 0;
	right: -405px;
	width: 405px;
    height: 100%;
    min-height: 100%;
	padding: 6% 45px 0px 45px;
    background-color: #fff;
    text-align: left;
    overflow: hidden;
    visibility: hidden;
    z-index: 9999;
    -webkit-backface-visibility: hidden;
    box-sizing: border-box;
	box-shadow: -3px 0 3px rgba(0, 0, 0, 0.04);
    @include eltdTransition(all .6s cubic-bezier(.77,0,.175,1));

    .eltd-sidearea-top-bottom-holder {
        @include eltdRelativeHolderLayout;
        height: 100%;

        .eltd-sidearea-top-holder {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
        }

        .eltd-sidearea-bottom-holder {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
        }
    }

    .eltd-close-side-menu-holder {
        position: absolute;
		top: 30px;
		right: 34px;
        z-index: 1000;
	
	    .admin-bar & {
		    top: 68px;
	    }
    }

	a.eltd-close-side-menu {
		display: block;
		color: $default-heading-color;
		font-size: 30px;
		z-index: 1000;
		@include eltdTransition(color .2s ease-out);

		&:hover {
			color: $default-text-color;
		}

		span {
			display: block;
			line-height: inherit;

			&:before {
				display: block;
				line-height: inherit;
			}
		}
	}
}

.eltd-side-menu-slide-from-right {
	
	&.eltd-right-side-menu-opened {
		
		.eltd-wrapper {
			
			.eltd-cover {
				z-index: 3000;
			}
		}
		
		.eltd-side-menu {
			right: 0;
			visibility: visible;

		}

	}

    .eltd-wrapper {

        .eltd-cover {
            z-index: -1;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            overflow: hidden;
            opacity: 0;
        }
    }
}
/* ==========================================================================
# Side area style - end
========================================================================== */